import React, { Component } from 'react';
import './SocialStyle.css';
import { data } from '../serverData/topic.js'
import { answer } from '../serverData/answer.js'

class SocialStyle extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: data.map(topic => topic = { options: Object.values(topic), isChecked: false }),
      answer: [],
      toastFlag: false,
      toastText: '',
      result: {
        nalyse: [],
        grade: ''
      }
    }
  }

  componentDidMount() {
    const data = this.state.data
    const domArr = this.refs.topicList.querySelectorAll('.topic')
    for (let i = 0; i < domArr.length; i++) {
      data[i].offsetTop = domArr[i].offsetTop
      data[i].height = domArr[i].offsetHeight
    }
    this.setState({
      data: data
    })
  }

  renderTopicList() {
    return this.state.data.map((topic, tIdx) =>
      <div className={`topic ${topic.className && !topic.isChecked ? topic.className : ''}`} key={tIdx}>
        <div className="title">{tIdx + 1}.</div>
        {
          topic.options.map((option, oIdx) => (
            <div 
              className={`item ${option.active ? 'active' : 'ordinary'}`} 
              key={oIdx}
              onClick={this.check.bind(this, tIdx, oIdx)}>
              <i className='icon'></i>{option.str} 
            </div>
          ))
        }
      </div>
    )
  }

  renderResultList() {
    return this.state.result.nalyse.map((nalyse, idx) => 
      <div key={idx}>{`${nalyse.str}: ${nalyse.num * 5}分`}</div>
    )
  } 

  renderResultDetail() {
    return this.state.answer.map((item, idx) => 
      <div className="result-detail-box" key={idx}>
        <div className="title-box">
          <div className="title">{item.str}</div>
          <div className="subtitle">{item.desc}</div>
        </div>
        <div className="detail-container">
          <div>
            <div className="top">你对自己的看法</div>
            <div className="bottom">{this.renderAnswerDetail(item.self)}</div>
          </div>
          <div>
            <div className="top">别人对你的看法</div>
            <div className="bottom">{this.renderAnswerDetail(item.others)}</div>
          </div>
        </div>
        
      </div>
    )
  }

  renderAnswerDetail(arr) {
    return arr.map((item, idx) => <div key={idx}>{item}</div>)
  }
   
  render() {
    return (
      <div className="social-style-container">
        <div className="header">
          <div className="title">社交风格问卷分析</div>
          <div className="sub-title">下面的每个题目中，快速选择一个跟你的行为方式最为贴近的描述。</div>
        </div>
        <div className="box" ref="topicList">
          {this.renderTopicList()}
        </div>
        <div className="btn" onClick={this.send.bind(this)}>提交</div>
        <div className="footer">
          <div className="title">你的分析结果为：<span>{this.state.result.grade}</span></div>
          <div className="result">
            {this.renderResultList()}
          </div>
          { this.state.answer.length > 0
            ? <div className="tip">以下主观、客观观点可自行根据分数对应分析</div>
            : ''
          }
          {this.renderResultDetail()}
        </div>
        {
          this.state.toastFlag 
          ? <div className="toast">{this.state.toastText}</div>
          : ''
        }
      </div>                                                                                                                      
    )
  }

  toast (str) {
    if (!str) {
      return false
    }
    this.setState({ 
      toastText: str,
      toastFlag: true
    })
    setTimeout(() => {
      this.setState({ 
        toastText: '',
        toastFlag: false
      })
    }, 1500)
  }

  warnning() {
    const data = this.state.data
    const unCheckedIdx = this.state.data.findIndex( topic => !topic.isChecked )
    if (unCheckedIdx > -1) {
      // 滚动到对应位置
      const centerLine = window.screen.availHeight / 2
      document.scrollingElement.scrollTop = data[unCheckedIdx].offsetTop - centerLine + data[unCheckedIdx].height / 2
      // 选中提示
      data[unCheckedIdx].className = 'warnning-border'
      this.setState({ 
        data: data
      }, () => {
        if (!this.state.data[unCheckedIdx].isChecked) {
          setTimeout(() => {
            this.setState({
              data: this.state.data.map((topic, tIdx) => topic = { ...topic, className: '' })
            })
          }, 1000)
        }
      })
    }
  }

  send() {
    this.setState({ 
      result: {...this.state.result, nalyse: []}
    })
    const checkedTopicArr = this.state.data.filter( topic => topic.isChecked )

    if (checkedTopicArr.length < 20) {
      this.warnning()
      this.toast('请将所有题目选择完毕！')
    } else {
      var resultObj = {
        A: { num: 0, str: '分析型' },
        B: { num: 0, str: '温和型' },
        C: { num: 0, str: '表现型' },
        D: { num: 0, str: '行动型' }
      }
      checkedTopicArr.forEach(topic => {
        const key = topic.options.filter( option => option.active )[0].value
        resultObj[key].num++
      })

      let resultList = Object.values(JSON.parse(JSON.stringify(resultObj)))
      // 按照 num 数值大小排序
      resultList = resultList.sort((a, b) => {
        const x = a.num
        const y = b.num
        return y - x
      })
      // 重复的删除并合并str
      resultList = resultList.reduce((prev, curr, idx, arr) => {
        if (prev.some((item) => item.num === curr.num)) {
          curr.str += `+${arr[idx - 1].str}`
          prev[prev.length - 1].str = curr.str
          return prev
        } else {
          return [...prev, curr]
        }
      }, [])
      console.log(resultList)
      // 根据选择结果分析
      const maxObj = resultList.reduce((prev, curr) => (prev.num > curr.num) ? prev : curr)
      // 将结果显示
      this.setState({ 
        result: {
          ...this.state.result,
          grade: maxObj.str,
          nalyse: Object.values(resultObj)
        }
      })
      this.setState({ 
        answer: answer
      })
      // 滚动到对应位置
      document.scrollingElement.scrollTop = this.state.data[this.state.data.length - 1].offsetTop
    }
  }

  check(tIdx, oIdx) {
    const data = this.state.data
    if (data[tIdx].options[oIdx].active) {
      this.toast('重复选择同一选项！')
      return false
    }
    data[tIdx].options.forEach((option, idx) => {
      data[tIdx].isChecked = true
      option = Object.assign(option, { active: idx === oIdx ? true : false })
    })
    this.setState({
      data: data
    })
  }
}

export default SocialStyle;
